@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700;900&display=swap);
*,
*:before,
*:after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul,
ol {
  list-style: none;
}

body {
  margin: 0;
  color: #333;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {
  width: 100%;
  max-width: 840px;
  padding: 0 1.25rem;
  margin: 0 auto;
}

.app-main {
  padding: 3.5rem 0;
}

/* HEADER */

.main-header {
  background-color: #f05455;
  padding: 0.63rem 0;
}

.main-header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  flex: 0 0 max-content;
}

.logo a {
  font-size: 2rem;
  color: #fff;
}

.header-nav {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: flex-end;
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.main-nav ul {
  display: flex;
  align-items: center;
}

.main-nav-link {
  color: #fff;
  font-size: 1.2rem;
  font-weight: 700;
  border-radius: 0.5rem;
  padding: 0.3rem 0.6rem;
  transition: 0.3s;
}

.main-nav-link:hover {
  background-color: #720304;
}

.cart-count {
  position: relative;
}

.cart-count svg {
  width: 2rem;
  color: #fff;
  transition: 0.3s;
}

.cart-count:hover svg {
  color: #720304;
}

.amount-container {
  position: absolute;
  top: -0.85rem;
  right: -0.85rem;
  width: 1.75rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.75rem;
  font-size: 1.25rem;
  color: #fff;
  border-radius: 50%;
  background: #720304;
  transition: 0.3s;
}

.cart-count:hover .amount-container {
  color: #720304;
  background-color: #fff;
}

/* PAGE CONTENT */

.page-top {
  margin-bottom: 2rem;
}
.page-title {
  font-size: 2.7rem;
  font-weight: 700;
  text-align: center;
  margin: 0;
}

.page-title::after {
  content: '';
  display: block;
  width: 3rem;
  border-top: 4px solid #f05455;
  margin: 1rem auto 0;
}

/* PRODUCT LIST */

.product-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, 380px), 1fr));
  grid-gap: 2.5rem;
  gap: 2.5rem;
}

.product-list-item {
  display: flex;
  align-items: center;
  grid-gap: 1.5rem;
  gap: 1.5rem;
  background-color: #f9f9f9;
  border-radius: 1rem;
  padding: 2rem;
  transition: 0.1s;
}

.product-list-item:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  box-shadow: 0 0 0 2px #000;
}

.product-list-item-img {
  position: relative;
  display: flex;
  flex: 0 0 140px;
}

.product-list-item-img img {
  width: 100%;
}

.image-overlay {
  position: absolute;
  inset: -3px;
  display: grid;
  place-items: center;
  font-size: 1.2rem;
  font-weight: 700;
  color: #333;
  background-color: rgba(255, 255, 255, 0.8);
  -webkit-backdrop-filter: blur(2px);
          backdrop-filter: blur(2px);
  transition: 0.3s;
  opacity: 0;
}

.product-list-item-img:hover .image-overlay {
  opacity: 1;
}

.product-list-item-title {
  color: #333;
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0 0 0.5rem;
}

.product-list-item-price {
  color: #f05455;
  font-size: 1.1rem;
  font-weight: 700;
  margin-bottom: 2rem;
}

.product-list-item-detail {
  flex: 1 1 auto;
}

.product-list-item-btn {
  font-size: 1rem;
  color: #fff;
  font-weight: 700;
  background: #5476f0;
  border-radius: 1rem;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: 0.3s;
}

.product-list-item-btn:hover {
  background-color: #f05455;
}

/* PRODUCT DETAIL */

.product-detail {
  display: grid;
  grid-template-columns: -webkit-max-content 1fr;
  grid-template-columns: max-content 1fr;
  grid-gap: 2rem;
  gap: 2rem;
  margin-top: 3rem;
}

.product-detail-count {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  grid-gap: 0.94rem 2.5rem;
  gap: 0.94rem 2.5rem;
}

.product-detail-info h2 {
  color: #f05455;
  font-size: 2rem;
  font-weight: 700;
}

.product-detail-info p {
  font-size: 1.2rem;
  line-height: 1.5;
}

.product-detail-add {
  font-size: 1.05rem;
  color: #fff;
  font-weight: 700;
  background: #5476f0;
  border-radius: 1rem;
  border: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: 0.3s;
}

.product-detail-add:hover {
  background: #f05455;
}

/* CART DETAILS */

.cart-item {
  display: flex;
  align-items: center;
  grid-gap: 1.8rem;
  gap: 1.8rem;
  border-bottom: 1px solid #d9d9d9;
  margin-bottom: 1.4rem;
  padding-bottom: 1.4rem;
}

.cart-item:last-child {
  border: none;
}

.cart-item-img {
  display: flex;
  flex: 0 0 50px;
}

.cart-item-img img {
  width: 100%;
}

.cart-item-info {
  display: flex;
  flex: 0 0 max-content;
  flex-direction: column;
  align-items: flex-end;
}

.cart-item-detail {
  flex: 1 1 auto;
}

.cart-item-title {
  font-size: 1.2rem;
  margin: 0 0 0.5rem;
}

.cart-item-price {
  font-size: 1.1rem;
  font-weight: 700;
  color: #8b8b8b;
}

.cart-item-count {
  display: flex;
  align-items: center;
  grid-gap: 0.65rem;
  gap: 0.65rem;
}

.cart-item-count-btn {
  display: grid;
  place-items: center;
  width: 2rem;
  height: 2rem;
  font-size: 1.1rem;
  border-radius: 100%;
  background: #d9d9d9;
  border: none;
  padding: 0;
  cursor: pointer;
  transition: 0.3s;
}

.cart-item-count-btn:hover {
  color: #fff;
  background: #5476f0;
}

.cart-item-amount {
  flex: 0 0 max-content;
  font-size: 1.2rem;
}

.cart-item-remove {
  display: flex;
  grid-gap: 5px;
  gap: 5px;
  font-size: 1rem;
  color: #f05455;
  background: none;
  border: none;
  border-radius: 0.3rem;
  padding: 0.5rem;
  cursor: pointer;
  transition: 0.3s;
}

.cart-item-remove:hover {
  color: #fff;
  background: #f05455;
}

.cart-total {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  grid-gap: 1rem;
  gap: 1rem;
  border-top: 2px solid #000;
  padding-top: 1rem;
}

.cart-total-price {
  font-size: 1.5rem;
  font-weight: 700;
}

.cart-total-clear {
  font-size: 1rem;
  color: #fff;
  background: #ee0505;
  border: none;
  border-radius: 0.3rem;
  padding: 0.5rem;
  cursor: pointer;
}

.cart-total-clear:hover {
  text-decoration: underline;
}

.continue-link {
  width: 200px;
  display: block;
  font-size: 1.2rem;
  color: #fff;
  text-align: center;
  background-color: #5476f0;
  border-radius: 0.3rem;
  padding: 0.5rem 1rem;
  margin: 0 auto;
  transition: 0.3s;
}

.continue-link:hover {
  background-color: #3f58b3;
}

/* MEDIA */

@media (max-width: 575px) {
  .logo a {
    font-size: 1.2rem;
  }
  .main-nav-link {
    font-size: 1rem;
  }
  .cart-count svg {
    width: 1.5rem;
  }
  .app-main {
    padding: 1.5rem 0;
  }
  .page-title {
    font-size: 2rem;
  }
  .product-list-item-img {
    flex: 0 0 120px;
  }
  .cart-item {
    flex-direction: column;
    text-align: center;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .cart-item-img img {
    max-height: 200px;
  }
}

